.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #e8505b;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Barlow", sans-serif;
  font-size: 1.6rem;
}

p {
  color: hsl(232deg, 10%, 55%);
  font-size: 1.8rem;
  line-height: 1.5;
}

.contenedor {
  width: 90%;
  max-width: 1300px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Fraunces", serif;
  font-weight: 900;
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.header {
  background-image: url(../img/mobile/image-header.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  min-height: 55rem;
  position: relative;
}
.header .header-content .header-bar {
  display: flex;
  justify-content: space-between;
  padding: 3rem 2rem;
}
.header .header-content .header-copy,
.header .header-content .arrow-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 0;
  right: 0;
}
.header .header-content .header-copy {
  top: 0;
  bottom: 30%;
}
.header .header-content .header-copy p {
  color: hsl(0deg, 0%, 100%);
  font-family: "Fraunces", serif;
  font-size: 4rem;
  text-transform: uppercase;
  text-align: center;
  font-weight: 900;
  letter-spacing: 0.5rem;
  line-height: 1.2;
}
.header .header-content .arrow-logo {
  bottom: 0;
  top: 30%;
}

.learn-more .about {
  height: 100vh;
  background-repeat: no-repeat;
  background-position: center top;
  background-size: 60vh;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
.learn-more .about .text {
  grid-row: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding: 3rem 2rem;
}
.learn-more .about .text h1, .learn-more .about .text p, .learn-more .about .text a {
  text-align: center;
}
.learn-more .about .text a {
  text-transform: uppercase;
  font-family: "Fraunces", serif;
}
.learn-more .brand {
  background-image: linear-gradient(to bottom, transparent 50%, hsl(0deg, 0%, 100%) 0%), url(../img/mobile/image-transform.jpg);
}
.learn-more .audience {
  background-image: linear-gradient(to bottom, transparent 50%, hsl(0deg, 0%, 100%) 0%), url(../img/mobile/image-stand-out.jpg);
}

.features .features-content .feature {
  background-repeat: no-repeat;
  background-position: center top;
  min-height: 60rem;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
.features .features-content .feature .text {
  padding: 3rem 2rem;
  grid-row: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}
.features .features-content .feature .text h1,
.features .features-content .feature .text p {
  text-align: center;
}
.features .features-content .feature .text h1 {
  font-size: 3rem;
}
.features .features-content .design {
  background-image: linear-gradient(to bottom, transparent 50%, transparent 0%), url(../img/desktop/image-graphic-design.jpg);
}
.features .features-content .design .text h1, .features .features-content .design .text p {
  color: hsl(167deg, 40%, 24%);
}
.features .features-content .photography {
  background-image: linear-gradient(to bottom, transparent 50%, transparent 0%), url(../img/desktop/image-photography.jpg);
}
.features .features-content .photography .text h1, .features .features-content .photography .text p {
  color: hsl(198deg, 62%, 26%);
}